import { useState } from "react";
import { useQuery, useMutation } from "@tanstack/react-query";
import { AdminSidebar } from "@/components/layout/admin-sidebar";
import { ThemeToggle } from "@/components/theme-toggle";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { StatCard } from "@/components/ui/stat-card";
import { TableSkeleton } from "@/components/ui/loading-spinner";
import { Input } from "@/components/ui/input";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useToast } from "@/hooks/use-toast";
import { queryClient, apiRequest } from "@/lib/queryClient";
import { formatCurrency, formatDate } from "@/lib/constants";
import { UserCircle, Trash2, ShoppingBag, Menu, Search, Download } from "lucide-react";

interface UserWithLastPurchase {
  id: string;
  name: string;
  email: string;
  phone: string | null;
  role: string;
  createdAt: string;
  lastPurchase: {
    date: string;
    amount: number;
    productType: string;
  } | null;
  totalPurchases: number;
  totalSpent: number;
}

export default function AdminUsers() {
  const { toast } = useToast();
  const [roleFilter, setRoleFilter] = useState<string>("all");
  const [searchQuery, setSearchQuery] = useState<string>("");
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [deleteUserId, setDeleteUserId] = useState<string | null>(null);
  const [deleteInactiveDays, setDeleteInactiveDays] = useState<number | null>(null);
  const [showDeleteInactiveDialog, setShowDeleteInactiveDialog] = useState(false);
  const [editedRoles, setEditedRoles] = useState<Record<string, string>>({});
  const [exportMode, setExportMode] = useState<string>("phone");

  const { data: users, isLoading } = useQuery<UserWithLastPurchase[]>({
    queryKey: ["/api/admin/users"],
    refetchInterval: 30000,
    refetchOnWindowFocus: true,
  });

  const deleteMutation = useMutation({
    mutationFn: (id: string) => apiRequest("DELETE", `/api/admin/users/${id}`, {}),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["/api/admin/users"] });
      queryClient.invalidateQueries({ queryKey: ["/api/admin/stats"] });
      toast({ title: "✅ User deleted successfully" });
      setDeleteUserId(null);
    },
    onError: (error: Error) => {
      toast({ 
        title: "Failed to delete user", 
        description: error.message, 
        variant: "destructive" 
      });
    },
  });

  const deleteInactiveMutation = useMutation({
    mutationFn: (days: number) => apiRequest("DELETE", `/api/admin/users/delete-inactive?days=${days}`, {}),
    onSuccess: (data: { deletedCount: number; errors?: string[] }) => {
      queryClient.invalidateQueries({ queryKey: ["/api/admin/users"] });
      queryClient.invalidateQueries({ queryKey: ["/api/admin/stats"] });
      toast({ 
        title: `✅ Deleted ${data.deletedCount} inactive users`,
        description: data.errors ? `Errors: ${data.errors.join(', ')}` : undefined
      });
      setDeleteInactiveDays(null);
    },
    onError: (error: Error) => {
      toast({ 
        title: "Failed to delete inactive users", 
        description: error.message, 
        variant: "destructive" 
      });
    },
  });

  const updateRoleMutation = useMutation({
    mutationFn: ({ id, role }: { id: string; role: string }) =>
      apiRequest("PATCH", `/api/admin/users/${id}/role`, { role }),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["/api/admin/users"] });
      toast({ title: "✅ User role updated successfully" });
    },
    onError: (error: Error) => {
      toast({
        title: "Failed to update user role",
        description: error.message,
        variant: "destructive"
      });
    },
  });

  const filteredUsers = users?.filter((user) => {
    // Role filter
    const roleMatch = roleFilter === "all" ||
      (roleFilter === "guest" && user.role === "guest") ||
      (roleFilter === "user" && user.role === "user") ||
      (roleFilter === "agent" && user.role === "agent") ||
      (roleFilter === "dealer" && user.role === "dealer") ||
      (roleFilter === "super_dealer" && user.role === "super_dealer") ||
      (roleFilter === "master" && user.role === "master") ||
      (roleFilter === "admin" && user.role === "admin");

    // Search filter
    const searchMatch = !searchQuery ||
      user.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      user.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
      (user.phone && user.phone.includes(searchQuery));

    return roleMatch && searchMatch;
  });

  const stats = {
    total: users?.length || 0,
    guests: users?.filter((u) => u.role === "guest").length || 0,
    users: users?.filter((u) => u.role === "user").length || 0,
    agents: users?.filter((u) => u.role === "agent").length || 0,
    dealers: users?.filter((u) => u.role === "dealer").length || 0,
      superDealers: users?.filter((u) => u.role === "super_dealer").length || 0,
      masters: users?.filter((u) => u.role === "master").length || 0,
    admins: users?.filter((u) => u.role === "admin").length || 0,
  };

  const formatExportPhone = (value: string | null) => {
    if (!value) return "";
    const digits = value.replace(/\D/g, "");
    if (!digits) return "";
    if (digits.startsWith("0")) return digits;
    if (digits.startsWith("233") && digits.length > 3) {
      return `0${digits.slice(3)}`;
    }
    if (digits.length === 9) {
      return `0${digits}`;
    }
    return `0${digits}`;
  };

  const formatExportPhoneCell = (value: string | null) => {
    const phone = formatExportPhone(value);
    return phone ? `="${phone}"` : "";
  };

  const handleExportUsers = () => {
    if (!users || users.length === 0) {
      toast({ title: "No users to export", variant: "destructive" });
      return;
    }

    const headers = exportMode === "phone"
      ? ["Phone"]
      : exportMode === "phone_email"
        ? ["Phone", "Email"]
        : ["Phone", "Username"];

    const rows = users.map((user) => {
      const phone = formatExportPhoneCell(user.phone);
      if (exportMode === "phone") {
        return [phone];
      }
      if (exportMode === "phone_email") {
        return [phone, user.email || ""];
      }
      return [phone, user.name || ""];
    });

    const csvContent = [headers, ...rows]
      .map((row) => row.map((value) => `"${String(value).replace(/"/g, '""')}"`).join(","))
      .join("\r\n");

    const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", `users-export-${exportMode}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="flex h-screen bg-background">
      {/* Mobile sidebar overlay */}
      {sidebarOpen && (
        <div className="fixed inset-0 z-50 md:hidden">
          <div className="fixed inset-0 bg-black/50" onClick={() => setSidebarOpen(false)} />
          <div className="fixed left-0 top-0 bottom-0 w-64 bg-sidebar border-r transform transition-transform duration-200 ease-in-out">
            <AdminSidebar onClose={() => setSidebarOpen(false)} />
          </div>
        </div>
      )}

      {/* Desktop sidebar */}
      <div className="hidden md:block">
        <AdminSidebar />
      </div>

      <div className="flex-1 flex flex-col overflow-hidden">
        <header className="flex items-center justify-between gap-4 h-16 border-b px-4 lg:px-6">
          <div className="flex items-center gap-4">
            <Button
              variant="ghost"
              size="icon"
              className="md:hidden"
              onClick={() => setSidebarOpen(true)}
            >
              <Menu className="h-5 w-5" />
            </Button>
            <h1 className="text-lg lg:text-xl font-semibold">Users</h1>
          </div>
          <ThemeToggle />
        </header>

        <main className="flex-1 overflow-y-auto p-4 lg:p-6">
          <div className="max-w-7xl mx-auto space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
              <StatCard
                title="Total Users"
                value={stats.total}
                icon={UserCircle}
                description="All registered users"
              />
              <StatCard
                title="Users"
                value={stats.users}
                icon={UserCircle}
                description="Regular users"
              />
              <StatCard
                title="Agents"
                value={stats.agents}
                icon={UserCircle}
                description="Agent accounts"
              />
              <StatCard
                title="Dealers"
                value={stats.dealers}
                icon={UserCircle}
                description="Dealer accounts"
              />
            </div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <StatCard
                title="Super Dealers"
                value={stats.superDealers}
                icon={UserCircle}
                description="Super dealer accounts"
              />
              <StatCard
                title="Guests"
                value={stats.guests}
                icon={UserCircle}
                description="Guest accounts"
              />
              <StatCard
                title="Admins"
                value={stats.admins}
                icon={UserCircle}
                description="Administrator accounts"
              />
            </div>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between gap-2">
                <div>
                  <CardTitle className="flex items-center gap-2">
                    <UserCircle className="h-5 w-5" />
                    All Users
                  </CardTitle>
                  <CardDescription>
                    Manage user accounts and track purchases
                  </CardDescription>
                </div>
                <div className="flex items-center gap-2">
                  <div className="flex items-center gap-2">
                    <Select value={exportMode} onValueChange={setExportMode}>
                      <SelectTrigger className="w-48">
                        <SelectValue placeholder="Export format" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="phone">Phone only</SelectItem>
                        <SelectItem value="phone_email">Phone + Email</SelectItem>
                        <SelectItem value="phone_username">Phone + Username</SelectItem>
                      </SelectContent>
                    </Select>
                    <Button size="sm" variant="outline" onClick={handleExportUsers}>
                      <Download className="h-4 w-4 mr-2" />
                      Export
                    </Button>
                  </div>
                  <div className="flex items-center gap-2">
                    <Input
                      type="number"
                      placeholder="Days"
                      className="w-20"
                      value={deleteInactiveDays || ""}
                      onChange={(e) => setDeleteInactiveDays(parseInt(e.target.value) || null)}
                    />
                    <Button
                      size="sm"
                      variant="destructive"
                      onClick={() => setShowDeleteInactiveDialog(true)}
                      disabled={!deleteInactiveDays}
                    >
                      Delete Inactive
                    </Button>
                  </div>
                  <div className="relative">
                    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                    <Input
                      placeholder="Search users..."
                      className="w-64 pl-10"
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                    />
                  </div>
                  <Select value={roleFilter} onValueChange={setRoleFilter}>
                    <SelectTrigger className="w-40">
                      <SelectValue placeholder="Role" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all">All Users</SelectItem>
                      <SelectItem value="guest">Guests</SelectItem>
                      <SelectItem value="user">Users</SelectItem>
                      <SelectItem value="agent">Agents</SelectItem>
                      <SelectItem value="dealer">Dealers</SelectItem>
                      <SelectItem value="super_dealer">Super Dealers</SelectItem>
                      <SelectItem value="master">Master</SelectItem>
                      <SelectItem value="admin">Admins</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </CardHeader>
              <CardContent>
                {isLoading ? (
                  <TableSkeleton rows={10} />
                ) : filteredUsers && filteredUsers.length > 0 ? (
                  <div className="overflow-x-auto">
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Name</TableHead>
                          <TableHead>Email</TableHead>
                          <TableHead>Phone</TableHead>
                          <TableHead>Role</TableHead>
                          <TableHead>Last Purchase</TableHead>
                          <TableHead>Total Purchases</TableHead>
                          <TableHead>Total Spent</TableHead>
                          <TableHead>Joined</TableHead>
                          <TableHead className="text-right">Actions</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {filteredUsers.map((user) => (
                          <TableRow key={user.id}>
                            <TableCell>
                              <div className="font-medium">{user.name}</div>
                            </TableCell>
                            <TableCell>
                              <div className="text-sm text-muted-foreground">{user.email}</div>
                            </TableCell>
                            <TableCell>
                              <div className="text-sm">{user.phone || "—"}</div>
                            </TableCell>
                            <TableCell>
                              <Select
                                value={editedRoles[user.id] || user.role}
                                onValueChange={(newRole) => {
                                  setEditedRoles(prev => ({ ...prev, [user.id]: newRole }));
                                }}
                                disabled={updateRoleMutation.isPending}
                              >
                                <SelectTrigger className="w-32">
                                  <SelectValue />
                                </SelectTrigger>
                                <SelectContent className="bg-white dark:bg-black border-2 border-gray-300 dark:border-gray-600 text-black dark:text-white">
                                  <SelectItem value="guest">Guest</SelectItem>
                                  <SelectItem value="user">User</SelectItem>
                                  <SelectItem value="agent">Agent</SelectItem>
                                  <SelectItem value="dealer">Dealer</SelectItem>
                                  <SelectItem value="super_dealer">Super Dealer</SelectItem>
                                  <SelectItem value="master">Master</SelectItem>
                                  <SelectItem value="admin">Admin</SelectItem>
                                </SelectContent>
                              </Select>
                              {editedRoles[user.id] && editedRoles[user.id] !== user.role && (
                                <Button
                                  size="sm"
                                  variant="outline"
                                  className="ml-2"
                                  onClick={() => {
                                    updateRoleMutation.mutate({ id: user.id, role: editedRoles[user.id] });
                                    setEditedRoles(prev => {
                                      const newEdited = { ...prev };
                                      delete newEdited[user.id];
                                      return newEdited;
                                    });
                                  }}
                                  disabled={updateRoleMutation.isPending}
                                >
                                  Save
                                </Button>
                              )}
                            </TableCell>
                            <TableCell>
                              {user.lastPurchase ? (
                                <div className="text-sm">
                                  <div className="font-medium">
                                    {formatCurrency(user.lastPurchase.amount)}
                                  </div>
                                  <div className="text-xs text-muted-foreground">
                                    {user.lastPurchase.productType} • {formatDate(user.lastPurchase.date)}
                                  </div>
                                </div>
                              ) : (
                                <span className="text-muted-foreground text-sm">No purchases</span>
                              )}
                            </TableCell>
                            <TableCell className="tabular-nums">
                              <div className="flex items-center gap-1">
                                <ShoppingBag className="h-3 w-3 text-muted-foreground" />
                                {user.totalPurchases}
                              </div>
                            </TableCell>
                            <TableCell className="font-medium tabular-nums">
                              {formatCurrency(user.totalSpent)}
                            </TableCell>
                            <TableCell className="text-muted-foreground text-sm">
                              {formatDate(user.createdAt)}
                            </TableCell>
                            <TableCell className="text-right">
                              <Button
                                size="sm"
                                variant="ghost"
                                className="text-destructive hover:text-destructive hover:bg-destructive/10"
                                onClick={() => setDeleteUserId(user.id)}
                                disabled={user.role === "admin"}
                              >
                                <Trash2 className="h-4 w-4" />
                              </Button>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>
                ) : (
                  <div className="text-center py-12 text-muted-foreground">
                    No users found.
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </main>
      </div>

      {/* Delete inactive users confirmation dialog */}
      <AlertDialog open={showDeleteInactiveDialog} onOpenChange={setShowDeleteInactiveDialog}>
        <AlertDialogContent className="bg-white dark:bg-slate-950 text-white dark:text-white">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-white">Delete Inactive Users</AlertDialogTitle>
            <AlertDialogDescription className="text-white">
              Are you sure you want to delete all users who haven't made a purchase in the last {deleteInactiveDays} days? 
              This action cannot be undone. All user data and transactions will be permanently removed.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={() => {
                if (deleteInactiveDays) {
                  deleteInactiveMutation.mutate(deleteInactiveDays);
                  setShowDeleteInactiveDialog(false);
                }
              }}
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
              disabled={deleteInactiveMutation.isPending}
            >
              Delete Inactive Users
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Delete confirmation dialog */}
      <AlertDialog open={!!deleteUserId} onOpenChange={(open) => !open && setDeleteUserId(null)}>
        <AlertDialogContent className="bg-white dark:bg-slate-950 text-white dark:text-white">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-white">Delete User</AlertDialogTitle>
            <AlertDialogDescription className="text-white">
              Are you sure you want to delete this user? This action cannot be undone.
              All user data and transactions will be permanently removed.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={() => deleteUserId && deleteMutation.mutate(deleteUserId)}
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
            >
              Delete
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}
