import { useState, useEffect } from "react";
import { useQuery, useMutation } from "@tanstack/react-query";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { AgentSidebarV2 as AgentSidebar } from "@/components/layout/agent-sidebar-v2";
import { ThemeToggle } from "@/components/theme-toggle";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage, FormDescription } from "@/components/ui/form";
import { useToast } from "@/hooks/use-toast";
import { queryClient, apiRequest } from "@/lib/queryClient";
import { Store, ExternalLink, Copy, Save, Smartphone, Menu } from "lucide-react";
import type { Agent } from "../../../../src/shared/schema";

const storefrontSchema = z.object({
  businessName: z.string().min(2, "Business name must be at least 2 characters"),
  businessDescription: z.string().min(10, "Business description must be at least 10 characters"),
  whatsappSupportLink: z.string().url("Please enter a valid WhatsApp link").optional().or(z.literal("")),
  whatsappChannelLink: z.string().url("Please enter a valid WhatsApp Channel link").optional().or(z.literal("")),
});

type StorefrontFormData = z.infer<typeof storefrontSchema>;

interface AgentProfileResponse {
  profile: Agent & {
    walletBalance: number;
    profitBalance: number;
    totalWithdrawals: number;
    role: string;
    user: {
      name: string | null;
      email: string | null;
      phone: string | null;
    };
  };
  stats: any;
}

export default function AgentStorefront() {
  const { toast } = useToast();
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const { data: profileData, isLoading: profileLoading } = useQuery<AgentProfileResponse>({
    queryKey: ["/api/profile"],
    refetchInterval: 30000, // Refresh every 30 seconds
    refetchOnWindowFocus: true,
  });
  
  const agent = profileData?.profile;

  const form = useForm<StorefrontFormData>({
    resolver: zodResolver(storefrontSchema),
    defaultValues: {
      businessName: "",
      businessDescription: "",
      whatsappSupportLink: "",
      whatsappChannelLink: "",
    },
  });

  useEffect(() => {
    if (agent) {
      form.reset({
        businessName: agent.businessName || "",
        businessDescription: agent.businessDescription || "",
        whatsappSupportLink: agent.whatsappSupportLink || "",
        whatsappChannelLink: agent.whatsappChannelLink || "",
      });
    }
  }, [agent, form]);

  const updateStoreMutation = useMutation({
    mutationFn: (data: StorefrontFormData) =>
      apiRequest("PATCH", "/api/agent/storefront", data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["/api/profile"] });
      toast({ title: "Store details updated successfully" });
    },
    onError: (error: Error) => {
      toast({ title: "Failed to update store", description: error.message, variant: "destructive" });
    },
  });

  const onSubmit = (data: StorefrontFormData) => {
    updateStoreMutation.mutate(data);
  };

  const copyStoreLink = () => {
    if (agent) {
      const url = `${window.location.origin}/store/agent/${agent.storefrontSlug}`;
      navigator.clipboard.writeText(url);
      toast({ title: "Store link copied to clipboard!" });
    }
  };

  return (
    <div className="flex h-screen bg-background">
      {/* Mobile sidebar overlay */}
      {sidebarOpen && (
        <div className="fixed inset-0 z-50 md:hidden">
          <div className="fixed inset-0 bg-black/50" onClick={() => setSidebarOpen(false)} />
          <div className="fixed left-0 top-0 bottom-0 w-64 bg-sidebar border-r transform transition-transform duration-200 ease-in-out">
            <AgentSidebar onClose={() => setSidebarOpen(false)} />
          </div>
        </div>
      )}

      {/* Desktop sidebar */}
      <div className="hidden md:block">
        <AgentSidebar />
      </div>

      <div className="flex-1 flex flex-col overflow-hidden">
        <header className="flex items-center justify-between gap-4 h-16 border-b px-4 lg:px-6">
          <div className="flex items-center gap-4">
            <Button
              variant="ghost"
              size="icon"
              className="md:hidden"
              onClick={() => setSidebarOpen(true)}
            >
              <Menu className="h-5 w-5" />
            </Button>
            <h1 className="text-lg lg:text-xl font-semibold">My Storefront</h1>
          </div>
          <ThemeToggle />
        </header>

        <main className="flex-1 overflow-y-auto p-4 lg:p-6">
          {/* Hero Section */}
          <div className="max-w-4xl mx-auto mb-8">
            <div className="relative bg-green-500 rounded-lg p-8 border border-green-600">
              <div className="flex items-center gap-4 mb-4">
                <div className="p-3 bg-white/20 rounded-full">
                  <Store className="h-8 w-8 text-white" />
                </div>
                <div>
                  <h2 className="text-2xl font-bold text-white">
                    {agent?.businessName || "Your Storefront"}
                  </h2>
                  <p className="text-green-100">
                    Manage and customize your online store
                  </p>
                </div>
              </div>

              <div className="bg-white/10 rounded-md p-4 border border-white/20">
                <p className="text-white/90 leading-relaxed">
                  {agent?.businessDescription || "No business description provided. Update it below to tell customers about your store."}
                </p>
              </div>

              <div className="flex items-center gap-4 mt-6">
                <div className="flex items-center gap-2 text-sm text-white/90">
                  <Smartphone className="h-4 w-4" />
                  <span>Active Storefront</span>
                </div>
                {agent?.storefrontSlug && (
                  <Badge variant="secondary" className="text-xs bg-white/20 text-white border-white/30">
                    /{agent.storefrontSlug}
                  </Badge>
                )}
              </div>
            </div>
          </div>

          <div className="max-w-4xl mx-auto space-y-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Store className="h-5 w-5" />
                  Store Link
                </CardTitle>
                <CardDescription>
                  Share this link with your customers
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex flex-col sm:flex-row sm:items-center gap-2">
                  <Input
                    value={agent ? `${window.location.origin}/store/agent/${agent.storefrontSlug}` : ""}
                    readOnly
                    className="font-mono text-sm flex-1 min-w-0"
                    data-testid="input-store-url"
                  />
                  <div className="flex items-center gap-2 sm:shrink-0">
                    <Button variant="outline" size="icon" onClick={copyStoreLink} data-testid="button-copy-link">
                      <Copy className="h-4 w-4" />
                    </Button>
                    {agent && (
                      <a href={`/store/agent/${agent.storefrontSlug}`} target="_blank" rel="noopener noreferrer" aria-label="Open storefront in new tab">
                        <Button variant="outline" size="icon" data-testid="button-open-store">
                          <ExternalLink className="h-4 w-4" />
                        </Button>
                      </a>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Store Details</CardTitle>
                <CardDescription>
                  Customize how your store appears to customers
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Form {...form}>
                  <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
                    <FormField
                      control={form.control}
                      name="businessName"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Business Name</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="Your business name"
                              disabled={profileLoading}
                              data-testid="input-business-name"
                              {...field}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="businessDescription"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Description</FormLabel>
                          <FormControl>
                            <Textarea
                              placeholder="Tell customers about your business..."
                              className="resize-none"
                              rows={3}
                              disabled={profileLoading}
                              data-testid="input-business-description"
                              {...field}
                            />
                          </FormControl>
                          <FormDescription>
                            This will be shown on your storefront page
                          </FormDescription>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="whatsappSupportLink"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>WhatsApp Support Link</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="https://wa.me/1234567890"
                              disabled={profileLoading}
                              data-testid="input-whatsapp-support"
                              {...field}
                            />
                          </FormControl>
                          <FormDescription>
                            Link to your WhatsApp for customer support. Leave empty to hide from storefront.
                          </FormDescription>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="whatsappChannelLink"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>WhatsApp Channel Link</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="https://whatsapp.com/channel/..."
                              disabled={profileLoading}
                              data-testid="input-whatsapp-channel"
                              {...field}
                            />
                          </FormControl>
                          <FormDescription>
                            Link to your WhatsApp Channel. Leave empty to hide from storefront.
                          </FormDescription>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <Button
                      type="submit"
                      disabled={updateStoreMutation.isPending || profileLoading}
                      data-testid="button-save-details"
                    >
                      <Save className="h-4 w-4 mr-2" />
                      {updateStoreMutation.isPending ? "Saving..." : profileLoading ? "Loading..." : "Save Changes to my storefront page"}
                    </Button>
                  </form>
                </Form>
              </CardContent>
            </Card>
          </div>
        </main>
      </div>
    </div>
  );
}
